@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700;800&display=swap);
*{
  box-sizing: border-box;
}

body {
  margin: 0;
  background: linear-gradient(#566b9b, #657fb9);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{width:100vw;height:100vh;text-align:center;background:url(../../static/media/background.98162c32.png) no-repeat center/cover;background-size:100% 100%}.freepik__copyrights{position:fixed;left:5px;bottom:15px;color:#cae7ff;font-size:1.5rem;font-weight:600}

.menu{width:620px;position:absolute;right:180px;top:30px;color:white;font-family:'Montserrat';font-size:1.7rem;font-weight:700}.menu__ul{list-style:none;padding:0;display:flex;justify-content:space-between}.menu__li{cursor:pointer}.menu__li:hover{text-decoration:underline}

.image{width:842px;height:605px;position:relative;left:100px;top:150px;background:url(../../static/media/image.6d993792.png) no-repeat;background-size:100% 100%}

.content{width:34%;position:absolute;right:147px;top:230px;font-family:'Montserrat';text-align:left;color:white}.content__title{margin:0;font-size:5.5rem;text-transform:uppercase;line-height:113%;font-weight:800;letter-spacing:3px}.content__text{margin:40px 0 5px 0;font-size:1.4rem;font-weight:400}.content__button{width:140px;height:40px;border-radius:20px;outline:none;border:none;color:#4d5a79;font-family:'Montserrat';font-weight:700;font-size:1rem}

